<template>
  <h2 hidden>{{state.circleid = $route.params.id }}</h2>
  <div hidden>
    <CircleUserList :circleid=state.circleid :roleSet="state.roleSet" />
  </div>
  <div class="row allCard">
    <form action="" v-if="state.roleSet.includes('ROLE_FOUNDER')" class="col">
      <div class="row">
        <div class="mb-3 col">
          <label for="inputName" class="form-label">그룹 이름</label>
          <input type="text" class="form-control" id="inputName" ref="circlename" v-model="state.name" autofocus>
        </div>
        <div class="mb-3 col">
          <label for="inputNumber" class="form-label">{{state.userCount}}/{{state.maxNum}}&nbsp;<img alt="Vue logo"
              src="../assets/icones/people.jpg" style="width:1.5em"></label>
          <div><input type="number" class="form-control" id="inputNumber" ref="maxNumber" v-model="state.maxNum"></div>
        </div>
        <div class="mb-3 col">
          <label for="inputPassword" class="form-label">비밀번호</label>
          <input type="text" class="form-control" id="inputPassword" v-model="state.pass">
        </div>
      </div>
      <div class="form-floating mb-3 col ">
        <textarea class="form-control" id="floatingTextarea2" v-model="state.content" style="height: 80px"></textarea>
        <label for="floatingTextarea2">그룹 설명</label>
      </div>
      <div class="item buttons col" ref="buttons" style="text-align:right">
        <button class="btn btn-primary" @click.prevent="modifyCircle">수정하기</button>
        <button type="button" class="btn btn-dark" @click="state.deleteModal = true">그룹 삭제하기</button>
        <button class="btn btn-light"><RouterLink to="/privateProfile" style="text-decoration:none;">프로필로 가기</RouterLink> </button>
      </div>
    </form>
    <div class="card col circleCard" v-else>
      <h5 class="card-header">그룹 이름: {{state.name}}</h5>
      <div class="card-body">
        <h5 class="card-title"></h5>
        <p v-if="state.content==''" class="card-text">그룹 설명 없음</p>
        <p v-else class="card-text">{{state.content}}</p>
        <div class="item buttons" ref="buttons" style="text-align:right">
          <span v-if="state.isAmember==false">
          </span>
          <span v-else>
            <button class="btn btn-dark" @click.prevent="state.showModal=true">나가기</button>
            <button class="btn btn-light"><RouterLink to="/privateProfile" style="text-decoration:none;">프로필로 가기</RouterLink> </button>
          </span>
        </div>
      </div>
    </div>
    &nbsp;&nbsp;
    <!-- <h2>회원목록</h2> -->
    <div class="card col">
      <div class="card-header">
        {{state.userCount}}/{{state.maxNum}}&nbsp;<img alt="Vue logo" src="../assets/icones/people.jpg" style="width:2.0em">회원
      </div>
      <div>
        <div class="image-container ">
      <div class="row">
        <div v-for="(list, i) in state.img" :key="(list, i)" class="col noSpace">
          <button v-if="state.roleSet.includes('ROLE_FOUNDER') &&!(state.email==state.lists[i].email)"
            @click="sendInfo(i) ,state.kickModal=true">x</button>
          <el-tooltip effect="dark" v-bind:content="state.lists[i].nickname" placement="top">
            <img v-if="state.lists[i].path == null" src="../assets/images/pictures/profile_icon.png"
              @click="showUserList(i)">
            <img v-else="!(state.lists[i].path == null)" v-bind:src="state.img[i]" alt="프로필사진" @click="showUserList(i)">
          </el-tooltip>
        </div>
      </div>
    </div>
      </div>
    </div>

    <!-- <div class="image-container col">
      <div class="row">
        <div v-for="(list, i) in state.img" :key="(list, i)" class="col noSpace">
          <button v-if="state.roleSet.includes('ROLE_FOUNDER') &&!(state.email==state.lists[i].email)"
            @click="sendInfo(i) ,state.kickModal=true">x</button>
          <el-tooltip effect="dark" v-bind:content="state.lists[i].nickname" placement="top">
            <img v-if="state.lists[i].path == null" src="../assets/images/pictures/profile_icon.png"
              @click="showUserList(i)">
            <img v-else="!(state.lists[i].path == null)" v-bind:src="state.img[i]" alt="프로필사진" @click="showUserList(i)">
          </el-tooltip>
        </div>
      </div>
    </div> -->
  </div><br><br>
  <div class="row">
    <div class="col">
      <NewsFeed :circleid="state.circleid" />
    </div>
    <div class="col" >
      <QuestionByCircle :circleid="state.circleid" :ismember="state.isAmember" />
    </div>
  </div>
  <Teleport to="body">
    <modal :show="state.deleteModal" @close="state.deleteModal = false">
      <template #header>
        <h2 style="color: red;">그룹 삭제</h2>
      </template>
      <template #body>
        <div>
          <h5>정말 {{ state.name }} 그룹을 삭제하시겠습니까?</h5>
        </div>
      </template>
      <template #footer>
        <el-button type="primary" plain @click="state.deleteModal = false">닫기</el-button>
        <el-button type="primary" plain @click="deleteCircle">그룹 삭제하기</el-button>
      </template>
    </modal>
  </Teleport>
  <Teleport to="body">
    <modal :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h2>나가기</h2>
      </template>
      <template #body>
        <div>
          <h5>정말로 {{state.name}} 그룹을 떠나시겠습니까??</h5>
        </div>
      </template>
      <template #footer>
        <el-button type="primary" plain @click="state.showModal=false">취소</el-button>
        <el-button type="primary" plain @click="leave">네 나갑니다</el-button>
      </template>
    </modal>
  </Teleport>
  <Teleport to="body">
    <!-- use the modal component, pass in the prop -->
    <modal :show="state.kickModal" @close="state.kickModal=false">
      <template #header>
        <h3>강제 퇴장</h3>
      </template>
      <template #body>
        <div class="item">
          <h5>정말 {{modal.name}}을(를) 강제 퇴장시키시겠습니까?</h5>
        </div>
      </template>
      <template #footer>
        <el-button type="secondary" plain @click="state.kickModal=false"> 닫기 </el-button>
        <el-button type="primary" plain @click="kickOut(modal.uid)">퇴장시키기</el-button>
      </template>
    </modal>
  </Teleport>
</template>
<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from 'vue'
import { useRouter } from 'vue-router';
import Modal from './teleport/Modal.vue'
import NewsFeed from './TheNewsFeed.vue'
import QuestionByCircle from './QuestionByCircle.vue'
import CircleUserList from './CircleUserList.vue'
export default {
  components: { Modal, NewsFeed, QuestionByCircle, CircleUserList, },
  setup() {
    onMounted(() => { getCircle(), isMember() })
    const router = useRouter();
    const state = reactive({
      roleSet: '',
      name: '',
      content: '',
      maxNum: '',
      userCount: '',
      pass: '',
      circleid: 'circleid',
      nickname: '',
      uid: '',
      lists: '',
      img: new Array(),
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem("TOKEN"),
      showModal: false,
      kickModal: false,
      deleteModal: false,
      isAmember: '',
    })
    const circlename = ref(null)
    const frm = ref(null)
    let notice = ref(null)
    let maxNotice = ref(null)
    let maxNumber = ref(null)
    const isMember = async () => {
      const url = "http://localhost:9090/api/isMember"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { circleid: state.circleid, email: state.email }
      await axios.post(url, body, { headers }).then(function (res) {
        state.isAmember = res.data
      }).catch((err) => { console.log('err:' + err) })
    }
    const getCircle = async () => {

      const url = "http://localhost:9090/api/mycircleread"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = { circleid: state.circleid, email: sessionStorage.getItem('email') }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {

          //get that from a circle
          state.pass = res.data.circleinfo.pass;
          state.name = res.data.circleinfo.name;
          state.content = res.data.circleinfo.content;
          state.maxNum = res.data.circleinfo.maxNum;

          state.roleSet = res.data.dto.roleSet;
          state.userCount = res.data.dto.userCount;
          // this couldbe
          state.nickname = res.data.dto.nickname;
          state.uid = res.data.dto.uid;
          state.lists = res.data.img;
          showImage(state.lists)
        } else {
          alert('fail')
        }
      })
    }
    const modifyCircle = async () => {
      if (circlename.value.value === '') {
        notice.value.innerHTML = 'Please input circle name'
        notice.value.style.color = 'red'; circlename.value.focus(); return false;
      }

      const url = "http://localhost:9090/api/circleModify"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        circleid: state.circleid,
        name: state.name,
        content: state.content,
        pass: state.pass,
        maxNum: state.maxNum,
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        alert('수정되었습니다')
      } else {
        alert('fail')
      }
    }
    const deleteCircle = async () => {
      const url = 'http://localhost:9090/api/deleteCircle'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { circleid: state.circleid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) { alert(state.name + " 그룹이 지워졌습니다.");state.deleteModal=false; router.push({ name: "network" }); }
      })
    }
    const leave = async () => {
      const url = 'http://localhost:9090/api/leaveCircle'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { circleid: state.circleid, email: sessionStorage.getItem('email') }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          alert( state.name +"그룹에서 탈퇴했습니다");
          state.showModal = false
          router.push({ name: "privateProfile" });
        }
      })
    }
    //TODO:get sure the userDTOchange when deleting one of them

    const showImage = async (file) => {
      const url = `http://localhost:9090/display`
      let str = "";
      console.info("Thumbnail" + file[0].thumbnailURL)
      console.info("Thumbnail" + file[1].thumbnailURL)
      for (let i = 0; i < file.length; i++) {
        str = `${url}?fileName=${file[i].thumbnailURL}`;
        state.img.push(str);
      }
    }
    const showUserList = async (i) => {
      if (JSON.stringify(state.uid) == JSON.stringify(state.lists[i].uid)) {
        router.push({ name: "privateProfile" });
      } else if (!(JSON.stringify(state.uid) == JSON.stringify(state.lists[i].uid))) {
        router.push({ name: 'publicProfile', params: { id: state.lists[i].uid } });
      }
    }
    const modal = reactive({
      name: '',
      uid: ''
    })
    const sendInfo = (i) => {
      modal.name = state.lists[i].nickname;
      modal.uid = state.lists[i].uid;
    }
    const kickOut = async (uid) => {

      const url = "http://localhost:9090/api/kickOut"
      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token
      }
      const body = { circleid: state.circleid, uid: modal.uid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data != null) {
          state.kickModal = false;
          router.go(0);
        }
      })
    }
    return { circlename, isMember, maxNotice, maxNumber, frm, notice, getCircle, state, modal, modifyCircle, deleteCircle, leave, showImage, showUserList, sendInfo, kickOut }
  }
}
</script>
<style scoped>
.image-container {
  background-color: rgba(6, 90, 92, 0.1);
  column-gap: 0px;
  position: relative;
  width: 100%;
  height: 100%;
}
.noSpace{
margin:0px; padding:0px;
column-gap: 0px;
}
.image-container img {
  cursor: pointer;
  width: 100px;
  height: 100px;
}

.image-container button {
  position: absolute;
  background-color: #555;
  color: white;
  font-size: 16px;
  padding: 5px 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.image-container button:hover {
  background-color: white;
  color: black;
}

input:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

textarea:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

.btn {
  margin: 0.7em;
}

.allCard {
  width: 100%;
  margin: 0px 0px 0px 0px;
}

.circleCard {
  margin: 0px;
  padding: 0px;
}
</style>